* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 300;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  background: #0097A7;
  width: 100%;
  height: 100%;
}

.container {
  text-align: center;
  display: flex;
  flex-direction:column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.container h1 {
  font-size: 40px;
  font-weight: 200;
}

